.navbar{
	background-color: #FFFBF5;
	
	mix-blend-mode: multiply;
}
.contest_main {
	display: flex;
	/* justify-content: space-between; */
	/* margin: auto; */
	width: 100vw;
	padding: 0% 5%;
	/* overflow: auto; */
	background-color: #FFFBF5;
	
	mix-blend-mode: multiply;

	padding-top: 5rem;
	/* column-gap: 2rem; */
	padding-bottom: 10rem;
	box-sizing: border-box;

}
.contest_box{
	display: flex;
	justify-content: center;
	width: calc(100vw - 20rem - 5%);
}
.contest_content{
	width: 100%;
	display: grid;
	grid-template-columns: repeat( auto-fill, minmax(265px, 280px) );
	justify-content: center;
    /* flex-wrap: wrap; */
	gap: 2.5vw;
}
.contest_card{
	padding: 5px;
	border: 2px solid #000000;
	box-shadow: 4px 4px 0px #1A1A1A;
}
.contest_card img{
    width: 265px;
	margin-left: 0;
}
.contest_button{
	margin-bottom: -34px;
	display: flex;
	flex-direction: row-reverse;
}
