/* mobile view */
@media(max-width:840px){
	.sidebar1{
		display: none;
	}
	.contest_main{
		padding: 0;
	}
	.contest_box{
		width: 100vw;
	}
}
@media(max-width:800px){
	.contest_box{
		width: 100vw;
	}
	.hamburger{
		display:block;
	}
	.hamburger.active .bar:nth-child(2){
		opacity: 0;
	}
	.hamburger.active .bar:nth-child(1){
		transform: translateY(8px) rotate(45deg);
	}
	.hamburger.active .bar:nth-child(1){
		transform: translateY(-8px) rotate(-45deg);
	}
	.mlist, .head{
		position: fixed;
		left: -100%;
		gap: 0;
		flex-direction: column; 
		width: 100%;
		text-align: center;
		transition: 0.3s;
	}
	.mlist.active{
		left: 0;

	}
	.head.active{
		left: 0;

	}

	.t112{
		display: none;
	}
	.r111{
		display: flex;
		flex-direction: column;
		height: fit-content;
		width: auto;
	}

	.g1{
		padding-left: 5px;
		padding-top: 5px;
		padding-bottom: 5px;
		padding-right: 5px;
		height: fit-content;
		margin-bottom: 100 px;
		position: relative;
		width: 100%;
		box-shadow: 2px 2px;
	}

	.Frame14859_Frame14859 .Group244 {
		width: 100%;
		margin-right: 0rem;
	}

	.Frame14859_Frame14859 {
		width: 100%;
		flex-flow: column;
		align-items: center;
	}
	
}

